/* eslint-disable import/prefer-default-export */
import { createContext, useContext } from 'react';

// 容器宽度变更 context，value 为容器宽度（类型 number）
// 注：当容器是 Table 时，value 是 table.data.colsWidth
export const ContainerWidthResizeContext = createContext<number>(-1);

export function useContainerWidthResize() {
  return useContext(ContainerWidthResizeContext);
}
